@mixin disable($op) {
	cursor: default;
	pointer-events: none;
	user-select: none;
	opacity: $op; }

#text {
	.field, .label, .d-flex {
		transition: all .2s ease-out; }
	&.is-inactive {
		.field:not(.text-align) {
			@include disable(0.5); }
		.text-align {
			.label, .d-flex:not(.add-text-outer) {
				@include disable(0.5); } } } }
.button {
	&.is-dark {
		background: #0a0a0a; } }

.action-editor {
	position: fixed;
	bottom: 70px;
	right: 40vw;
	width: 250px;
	height: 300px;
	z-index: 1;
	box-shadow: 1px 2px 2px 1px rgba(0,0,0,.2);
	background: #e8e8e8;
	padding: 15px 10px;
	border-radius: 20px;
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	transition: all .25s ease-out;
	@include grab-cursor;
	&.simple-options {
		.border-field,
		.border-width-field,
		.rect-bg {
			@include disable(0.2); } }
	&.mid-options {
		.border-field,
		.border-width-field {
			@include disable(0.2); } }
	&:active {
		@include grabbing-cursor; }
	&.is-visible {
		opacity: 1;
		visibility: visible;
		pointer-events: all; }
	.w100 {
		&:not(:first-child) {
			margin-top: 15px; } }
	.d-flex {
		.colors-wrap {
			justify-content: center; }
		.field {
			margin-right: 20px;
			margin-bottom: 0; } }
	.range-slider-value {
		color: #292929;
		font-weight: bold; }
	.range-slider-range {
		width: calc(100% - 30px);
		background: #fff;
		&::-moz-range-track {
			background: #fff; }
		&::-webkit-slider-thumb {
			background: #fff;
			&:hover {
				background: #fff; } }
		&:active::-webkit-slider-thumb {
			background: #fff; }
		&::-moz-range-thumb {
			&:hover {
				background: #fff; } }
		&:active::-moz-range-thumb {
			background: #fff; } }
	&-inner {
		position: relative;
		width: 100%;
		height: 100%; }
	&-close {
		position: absolute;
		top: 0;
		right: 0;
		cursor: pointer; } }

.view-options,.action-buttons {
	.button {
		&.is-active {
			opacity: 1 !important;
 }			// background: red
		&.is-hover {
			opacity: 0.5;
			transition: .2s;
			&:hover {
				opacity: 1; } }
		&.is-disabled {
			cursor: default !important;
			pointer-events: none;
			user-select: none;
			opacity: 0.5; }
		&:not(:last-child) {
			margin-right: 10px; }
		&.is-rounded {
			width: 35px;
			height: 35px;
			border-radius: 50%;
			position: relative;
			i {
				font-size: 18px;
				padding-bottom: 3px;
				@include vcenter; } } } }

.expand-options,.view-buttons {
	position: absolute;
	bottom: 20px; }

.view-buttons {
	right: 20px; }

.expand-options {
	left: 20px;
	&.is-active {
		.expand-options-trigger {
			opacity: 1; }
		.expand-options-inner {
			opacity: 1;
			visibility: visible;
			pointer-events: all;
			transform: translateY(6px); } }
	&-trigger {
		position: absolute;
		bottom: -5px;
		left: -5px;
		opacity: 0.5;
		transition: all .2s ease-out;
		&:hover {
			opacity: 1; } }
	&-inner {
		transform: translateY(100px);
		display: flex;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		transition: all .4s ease-out;
		margin-left: 50px; }
	.button {
		display: block; } }

.action-buttons {
	#clear-button {
		i {
			font-size: 22px; } }
	#undo-button {
		i {
			margin-left: 3px; } }
	#redo-button {
		i {
			margin-left: 1px; } } }
.rel {
	position: relative; }

.action-container,
.view-container {
	@include coverdiv;
	canvas {
		@include coverdiv; } }


.box-upload {
	position: relative;
	input {
		display: none; }
	label {
		cursor: pointer;
		@include coverdiv; } }

.text-tab {
	.columns {
		> .column {
			&:first-child {
				padding-right: 25px; }
			&:last-child {
				padding-left: 25px; } } } }

.tile.is-child.options-box {
	padding: 0;
	background-color: transparent;
	display: flex;
	flex-direction: column;
	height: 43%;
	.tab-content {
		height: 100%; } }

.tabs {
	&:not(:last-child) {
		margin-bottom: .8rem; }
	ul {
		flex-wrap: wrap;
 }		// background: #e8e8e8
	&.is-toggle {
		li {
			height: 45px;
			&.is-active a {
				background: #0a0a0a;
				border-color: #0a0a0a; } }
		a {
			color: #000;
			padding: 0.5em;
			background: #f2f2f2;
			border: none !important;
			// border-color: transparent !important
			&:hover {
				background: #0a0a0a;
				color: #fff; }
			span {
				line-height: 1.2;
				padding-bottom: 5px; } } } }
.field:not(:last-child) {
	margin-bottom: 20px; }
.label {
	color: #292929; }


.dropdown {
	width: 100%;
	&-trigger, .button,&-menu {
		width: 100%; }
	&-menu {
		border: none;
		top: -2px; }
	&-content {
		box-shadow: 0 1px 5px 0 rgba(0,0,0,.12)!important;
		border: none; }
	&-item {
		&:hover {
			background: rgba(0,0,0,0.04); } }
	.button {
		justify-content: flex-start;
		border-radius: 2px;
		padding: 11px 15px;
		height: auto;
		border: none;
		outline: none !important;
		box-shadow: 0 1px 5px 0 rgba(0,0,0,.12)!important;
		.icon {
			@include center(v);
			right: 15px;
			color: $light; } } }

.dropdown-item {
	cursor: pointer; }


.input, .textarea,.button {
	border-color: transparent !important;
	box-shadow: $bxsh !important;
	&:hover {
		border-color: transparent !important; } }

.button {
	&:active {
		box-shadow: none !important; } }

.d-flex {
	display: flex; }

.range-slider {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	input {}
	&::-moz-focus-inner, &::-moz-focus-outer {
		border: 0; }
	&-value {
		cursor: default;
		display: inline-block;
		position: relative;
		color: #b5b5b5;
		line-height: 1.2;
		text-align: center; }
	&-range {
		-webkit-appearance: none;
		width: calc(100% - 50px);
		height: 3px;
		border-radius: 5px;
		background: #d7dcdf;
		outline: none;
		padding: 0;
		margin: 0;
		margin-top: 3px;
		&::-moz-range-track {
			background: #d7dcdf;
			border: 0; }
		&::-webkit-slider-thumb {
			appearance: none;
			width: 15px;
			height: 15px;
			border-radius: 50%;
			background: #ccc;
			cursor: pointer;
			transition: all 0.15s ease-in-out;
			&:hover {
				background: $blue; } }
		&:active::-webkit-slider-thumb {
			background: $blue;
			width: 20px;
			height: 20px; }
		&::-moz-range-thumb {
			width: 20px;
			height: 20px;
			display: none;
			border: 0;
			border-radius: 50%;
			background: #2c3e50;
			cursor: pointer;
			transition: all 0.15s ease-in-out;
			&:hover {
				background: $blue; } }
		&:active::-moz-range-thumb {
			background: $blue;
			width: 20px;
			height: 20px; }
		// &:focus::-webkit-slider-thumb
 } }		/// box-shadow: 0 0 0 3px #fff, 0 0 0 6px $blue











