$tile: #eee;
$bd: #cecece;
$green: #8dc63f;
$light: rgba(0,0,0,0.44);
$blue: #1e88e5;
$bxsh: 0px 1px 5px 0px rgba(0,0,0,0.12);
$mi: 'Material Icons';
body,html {
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	position: relative;
	font-size: 15px;
	color: #232323;
	background: #fafafa;
	font-family: 'Oswald', sans-serif; }

.section {
	padding: 2rem 2rem 1rem; }
.hero {
	// height: calc(100vh - 56px)
	height: 100vh;
	.container,.tile {
		height: 100%; }
	.container {
		margin: 0; } }

.h100 {
	height: 100%;
	position: relative; }

.fd-columns {
	flex-direction: column; }
.w100 {
	width: 100%; }
.tile {
	&.box {
		background: $tile;
		border: none;
		box-shadow: none;
		&.rel {
			border-radius: 20px;
			overflow: hidden;
			box-shadow: $bxsh; } }
	&.is-parent {
		padding: 0.5rem; } }

.tile.is-vertical > .tile.is-child:not(:last-child) {
	margin-bottom: 1rem !important; }

.tab-content {
  display: none;
  .columns {
  	margin: 0 !important;
  	height: 100%; }
  .column {
  	padding-top: 5px;
  	height: 100%;
  	padding-bottom: 0; }
  &.current-tab {
    display: block; } }

.mb0 {
	margin-bottom: 0 !important; }
.pt0 {
	padding-top: 0 !important; }
.px0 {
	padding-left: 0 !important;
	padding-right: 0 !important; }

.bg0 {
	background: transparent !important; }
.bxsh {
	box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12); }

.colors {
	&.background {
		.colors-single {
			margin-right: 12px;
			margin-bottom: 15px; }
		.colors-input {
			margin-top: 10px; } }
	&-input {
		margin-top: 5px;
		input {
			text-transform: uppercase; } }
	&-wrap {
		display: flex;
		flex-flow: row wrap; }
	&-single {
		cursor: pointer;
		width: 42px;
		height: 42px;
		border-radius: 50%;
		position: relative;
		overflow: hidden;
		margin-bottom: 10px;
		&:not(:last-child) {
			margin-right: 10px; }
		&[data-color="#FFFFFF"] {
			box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.12); }
		&:not(.add-color) {
			&:after {
				content: 'done';
				@include vcenter;
				font-family: $mi;
				width: 100%;
				height: 100%;
				background: rgba(0,0,0,.1);
				display: flex;
				align-items: center;
				justify-content: center;
				font-size: 20px;
				color: #fff;
				opacity: 0;
				visibility: hidden;
				transform: translateY(50%) translateX(-50%);
				transition: all .27s ease-out;
				border-radius: 50%; }
			&.is-active {
				&:after {
					opacity: 1;
					visibility: visible;
					transform: translateY(-50%) translateX(-50%); } } } } }


.add-color {
	label {
		display: inline-block;
		position: relative;
		width: 100%;
		height: 100%;
		cursor: pointer;
		overflow: hidden;
		border-radius: 50%; }
	input[type="color"] {
		opacity: 0;
		visibility: hidden;
		-webkit-appearance: none;
		border: none;
		background: #fff;
		width: 100px;
		height: 100px;
		@include vcenter;
		border-radius: 50%;
		overflow: hidden;
		outline: none;
		cursor: inherit;
		&::-webkit-color-swatch-wrapper {
			padding: 0; }
		&::-webkit-color-swatch {
			border: none;
			border-radius: 50%; }
		&::-moz-focus-inner {
			border: none;
			padding: 0;
			border-radius: 50%; }
		&::-moz-color-swatch {
			border: none;
			border-radius: 50%;
			height: 140px; } }
	.circle {
		position: absolute;
		top: 2px;
		left: 2px;
		width: 38px;
		height: 38px;
		background-color: #fff;
		border-radius: 50%;
		cursor: inherit;
		color: #222;
		box-shadow: $bxsh;
		&:after {
			content: 'add';
			font-family: $mi;
			@include vcenter;
			font-size: 15px;
			margin-top: 1px; } } }



.text-align {
	margin-top: 30px;
	.add-text-outer {
		width: 100%;
		align-items: center;
		height: 100%;
		.button {
			border-radius: 4px; } }
	.control {
		display: flex;
		align-items: center;
		justify-content: flex-start; }
	.d-flex {
		align-items: center;
		justify-content: center; }
	.btn-group {
		box-shadow: $bxsh;
		width: max-content;
		display: inline-flex;
		.button {
			box-shadow: none !important;
			i {
				color: #ccc; }
			&.is-active {
				background: #e8e8e8;
				i {
					color: #000; } } }
		&:last-child {
			margin-left: 15px;
			.button {
				padding-top: 8px; } } }
	.button {
		display: inline-block;
		margin: 0;
		border-radius: 0; } }

.tab-content {
	.box {
		width: 100%;
		margin-bottom: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		cursor: pointer;
		border: none;
		box-shadow: $bxsh;
		position: relative;
		&:hover {
			.box-hover {
				opacity: 1;
				visibility: visible;
				transition: all .3s ease-out; } }
		&-title {
			text-align: center;
			font-weight: 600;
			line-height: 1.2; }
		&-gray {
			background: #eee; }
		&-hover {
			opacity: 0;
			visibility: hidden;
			@include coverdiv; }
		&:active {
			box-shadow: none !important; }
		&:not(:last-child) {
			margin-right: 10px; }
		i {
			font-size: 50px;
			color: #0a0a0a; } } }

.geometry-tab {
	.field {
		margin-bottom: 0 !important; }
	.w100 {
		&:last-child {
			margin-bottom: 30px; } }
	.d-flex {
		> div:not(:first-child) {
			margin-left: 30px; } }
	.columns {
		flex-direction: column;
		justify-content: center; }
	.box {
		flex: 0 1 calc(32% - 10px);
		height: 45%;
		margin-right: 0 !important; }
	.column {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		&:first-child {
			flex-grow: 1; }
		&:last-child {
			flex-grow: 2;
			margin-top: 10px; } } }

.image-tab,.background-tab {
	.box {
		flex: 0 1 calc(23% - 10px);
		margin: 7px 0; } }

.image-tab,.background-tab, .pattern-tab {
	.columns {
		flex-wrap: wrap;
		justify-content: space-between;
		.box {
			margin-right: 0;
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			&-gray {
				display: flex;
				align-items: center; } } } }


.pattern-tab {
	.columns {
		padding-top: 25px;
		align-items: flex-start;
		justify-content: flex-start;
		.box {
			flex: 0 1 calc(5% - 10px);
			margin-right: 23px;
			height: 15%;
			i {
				font-size: 30px;
				margin-top: 5px; } } } }
.image-tab {
	.box-hover {
		background-color: #fff;
		display: flex;
		align-items: center;
		i {
			font-size: 20px;
			@include vcenter; }
		a {
			color: #333;
			line-height: 1.2;
			@include coverdiv;
			padding: 15px 10px;
			text-transform: uppercase;
			font-size: 11px; } } }

.background-tab {
	.box {
		height: 30%; } }
